    #flyoutMenu {
        display: flex;
        flex-direction: column;
        cursor: default;
    }
    
        #flyoutMenu h1 {
            height: 10%;
            font-size: 3.5em;
        }
            .close {
                display: unset;
                position: absolute;
                top: 0.75%;
                left: 2%;
                width: 32px;
                height: 32px;
                opacity: 0.7;
                z-index: 100;
                color: white;
              }
              .close:hover {
                opacity: 1;
                cursor: pointer;
              }
              .close:before, .close:after {
                position: absolute;
                left: 15px;
                content: ' ';
                height: 33px;
                width: 2px;
                background-color: white;
              }
              .close:before {
                -webkit-transform: rotate(45deg);
                        transform: rotate(45deg);
              }
              .close:after {
                -webkit-transform: rotate(-45deg);
                        transform: rotate(-45deg);
            }
    }


:root { 
    --background: white;
    --card-top: rgba(29, 209, 161,1.0);
  }
  #card-container {
    background: white;
    background: var(--background);
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
    position: relative;
    background: white;
    box-shadow: 5px 5px 5px rgba(0,0,0,0.1), 0.5px 0.5px 5px 1px rgba(0,0,0,0.05);
    width: 17em;
    height: 25em;
    -webkit-transition: 0.7s;
    transition: 0.7s;
    display: grid;
    grid-template-rows: repeat(2, 50%); 
    /* margin-top: 1em; */
    margin: .5em 4em .5em 4em;
    /* border: 2px solid orange;  */
    cursor: url(/joeljames/static/media/icons8-checkmark.8393e06f.svg), auto;
    z-index: 5;
  }
  
  .card-top {
    background: rgba(29, 209, 161,1.0);
    background: var(--card-top);
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 33% 33% 33%;
    grid-template-areas: 
      "lang lang"
      "title title"
      "author author"
  
  }
  
  .card-top > * {
    padding-left: 1em;
    padding-right: 1em;
  /*   border: 1px dashed black; */
    font-weight: 700;
  }
  
  .tut-lang {
    grid-area: lang;
    padding-top: 1em;
    color: rgba(45, 52, 54,1.0);
  }
  
  .card-title {
    font-size: 1.30em;
    /* color: orange; */
    grid-area: title;
    color: rgba(45, 52, 54,1.0);
    align-self: start;
    /* padding-top: 0.5em; */
    /* border: 2px solid pink; */

  }
  
  .card-ref {
    grid-area: author;
    /* border: 2px solid orange; */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

  }

  .card-ref p {
    color: rgba(45, 52, 54,1.0);
    font-size: 0.75em;
  }

  /* Lame hover over the text in the cards to see it more clearly */
