    box-shadow: 5px 5px 5px rgba(0,0,0,0.2), 0.5px 0.5px 5px 1px rgba(0,0,0,0.1);

  } */
  
  .card-bottom img {
    width: 100%;
    height: 100%;
  }

  .card-bottom {
    overflow: hidden;

  }

  .card-bottom a {
    overflow: hidden;
    cursor: url(/joeljames/static/media/icons8-checkmark.8393e06f.svg), auto;

  }

  #card-container:hover {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      box-shadow: 5px 5px 5px rgba(0,0,0,0.19), 1px 1px 5px 1px rgba(0,0,0,0.12);
      cursor: url(/joeljames/static/media/icons8-checkmark.8393e06f.svg), auto;
      z-index: 6;
  }

  .fa {
